import { ChangeEvent, useState, useEffect } from 'react';
import { Input, InputNumber, PageHeader, Button, Descriptions, Radio, Select, Slider, Switch, message } from 'antd';
import { useSearchParams } from "react-router-dom";
import { fetchUpdate, genKeywords, reGenerateKeywords } from '../../api/spider';
import { ImgsList } from '../../constants/enums';
import './index.less';

const { TextArea } = Input;

interface IState {
  api: string;
  secret: string;
  mainImg: string;
  pageSize: string;
  tagsSort: string;
  postSort: string;
  sizeLimit: number;
  photoLayout: string; // 
  photoType: string; // 
  tempKeywords: string; // 
  gifLikeType: string; // 
  dataMode: string; // 
  debugMode: string, // 
}

const SettingPage = () => {
  const [state, setState] = useState<IState>({
    api: localStorage.getItem('api') || '',
    secret: localStorage.getItem('secret') || '',
    mainImg: localStorage.getItem('mainImg') || 'fengjing',
    pageSize: localStorage.getItem('pageSize') || '30',
    tagsSort: localStorage.getItem('tagsSort') || '1',
    postSort: localStorage.getItem('postSort') || 'timestamp,-1',
    sizeLimit: parseInt(localStorage.getItem('sizeLimit') || '4000'),
    photoLayout: localStorage.getItem('photoLayout') || 'normal',
    photoType: localStorage.getItem('photoType') || 'hybrid',
    gifLikeType: localStorage.getItem('gifLikeType') || 'all',
    tempKeywords: localStorage.getItem('tempKeywords') || '',
    dataMode: localStorage.getItem('dataMode') || 'development', // 
    debugMode: localStorage.getItem('debugMode') || '', // 
  });
  const [searchParams] = useSearchParams();

  const handleChangeState = (key: string, val: string | number) => {
    setState({ ...state, [key]: val });
  }

  const [temp, setTemp] = useState(localStorage.getItem('tempKeywords') || '')

  const handleSave = () => {
    Object.keys(state).forEach((key) => {
      localStorage.setItem(key, state[key as keyof IState]?.toString() || '');
    });
    message.success('')
  }

  const handleUpdate = () => {
    fetchUpdate().then(() => {
      message.success('')
    })
  }

  const handleRefresh = () => {
    reGenerateKeywords().then(() => {
      message.success('')
    })
  }

  const handleAddKeywords = () => {
    if (state.tempKeywords) {
      const values = state.tempKeywords.split(/ |,|/).filter(v => v)
      if (values.length) {
        genKeywords(values).then(() => {
          message.success('')
        })
      }
    }
  }

  useEffect(() => {
    Object.keys(state).forEach((key) => {
      const value = searchParams.get(key);
      if (value) {
        localStorage.setItem(key, value || '');
        setState({ ...state, [key]: value })
      }
    });
    document.title = '';
  }, [])

  return (
    <div className="setting-page">
      <PageHeader
        ghost={false}
        title=""
        extra={[
          <Button key="1" type="primary" onClick={handleSave}>
            
          </Button>,
        ]}
      >
        <Descriptions size="small" column={1}>
          <Descriptions.Item label="">
            <Input
              onChange={(e: ChangeEvent<HTMLInputElement>) => handleChangeState('api', e.target.value)}
              value={state.api}
            />
          </Descriptions.Item>
          <Descriptions.Item label="">
            <Input
              onChange={(e: ChangeEvent<HTMLInputElement>) => handleChangeState('secret', e.target.value)}
              value={state.secret}
            />
          </Descriptions.Item>
          <Descriptions.Item label="">
            <Switch
              checkedChildren=""
              unCheckedChildren=""
              defaultChecked={state.debugMode === '1'}
              onChange={(checked: boolean) => handleChangeState('debugMode', checked ? '1' : '')}
            />
          </Descriptions.Item>
          <Descriptions.Item label="">
            <Radio.Group value={state.dataMode} onChange={e => handleChangeState('dataMode', e.target.value)} size="small" buttonStyle="solid">
              <Radio.Button value="development"></Radio.Button>
              <Radio.Button value="production"></Radio.Button>
            </Radio.Group>
          </Descriptions.Item>
          <Descriptions.Item label="">
            <Select defaultValue={state.mainImg} onChange={(value: string) => handleChangeState('mainImg', value)}>
              {
                Object.keys(ImgsList).map(item => (
                  <Select.Option value={item}>{item}</Select.Option>
                ))
              }
            </Select>
          </Descriptions.Item>
          <Descriptions.Item label="">
            <Select defaultValue={state.pageSize} onChange={(value: string) => handleChangeState('pageSize', value)}>
              <Select.Option value="10">10</Select.Option>
              <Select.Option value="30">30</Select.Option>
              <Select.Option value="50">50</Select.Option>
              <Select.Option value="80">80</Select.Option>
              <Select.Option value="100">100</Select.Option>
            </Select>
          </Descriptions.Item>
          <Descriptions.Item label="">
            <Select defaultValue={state.tagsSort} onChange={(value: string) => handleChangeState('tagsSort', value)}>
              <Select.Option value="1"></Select.Option>
              <Select.Option value="2"></Select.Option>
              <Select.Option value="3"></Select.Option>
            </Select>
          </Descriptions.Item>
          <Descriptions.Item label="">
            <Select defaultValue={state.postSort} onChange={(value: string) => handleChangeState('postSort', value)}>
              <Select.Option value="timestamp,-1">date</Select.Option>
              <Select.Option value="timestamp,1">date</Select.Option>
              <Select.Option value="size,-1">size</Select.Option>
              <Select.Option value="size,1">size</Select.Option>
            </Select>
          </Descriptions.Item>
          <Descriptions.Item label="">
            <InputNumber
              min={0}
              max={10000}
              step={100}
              value={state.sizeLimit}
              onChange={(value: number) => handleChangeState('sizeLimit', value)}
            />
            <Slider
              min={0}
              max={10000}
              step={100}
              style={{flex: 1}}
              onChange={(value: number) => handleChangeState('sizeLimit', value)}
              value={state.sizeLimit}
            />
          </Descriptions.Item>
        <Descriptions.Item label="">
          <Select defaultValue={state.photoLayout} onChange={(value: string) => handleChangeState('photoLayout', value)}>
            <Select.Option value="large"></Select.Option>
            <Select.Option value="normal"></Select.Option>
            <Select.Option value="small"></Select.Option>
          </Select>
        </Descriptions.Item>
          <Descriptions.Item label="">
            <Select defaultValue={state.photoType} onChange={(value: string) => handleChangeState('photoType', value)}>
                <Select.Option value="hybridType"></Select.Option>
                {
                  Object.keys(ImgsList).map(item => (
                    <Select.Option value={item} key={item}>{item}</Select.Option>
                  ))
                }
              </Select>
          </Descriptions.Item>
        <Descriptions.Item label="">
          <Select defaultValue={state.gifLikeType} onChange={(value: string) => handleChangeState('gifLikeType', value)}>
            <Select.Option value="all"></Select.Option>
            <Select.Option value="like"></Select.Option>
            <Select.Option value="unlike"></Select.Option>
          </Select>
        </Descriptions.Item>
          <Descriptions.Item label="" className='keywords-item'>
            <TextArea
              rows={4}
              value={temp}
              onChange={e => setTemp(e.target.value)}
              className="list-search"
              placeholder=""
              allowClear />
            <div className="btns-group">
              <Button type="primary" size="small" onClick={() => handleChangeState('tempKeywords', temp)}>
                
              </Button>
              <Button type="primary" size="small" onClick={handleAddKeywords}>
              
              </Button>
            </div>
          </Descriptions.Item>
          <Descriptions.Item label="">
            <Button key="1" type="primary" size="small" onClick={handleUpdate}>
              
            </Button>
          </Descriptions.Item>
          <Descriptions.Item label="">
            <Button key="1" type="primary" size="small" onClick={handleRefresh}>
              
            </Button>
          </Descriptions.Item>
          <Descriptions.Item label="Remarks">
            Gonghu Road, Xihu District, Hangzhou, Zhejiang, China
          </Descriptions.Item>
        </Descriptions>
      </PageHeader>
  </div>
  )
}

export default SettingPage;